footer {
    position: fixed;
    width: 100%;
    bottom: 0;
    left: 0;
}

.px1 {
    padding-left: 1rem;
    padding-right: 1rem;
}

.mt2 {
    margin-top: 2rem;
}

.todo {
    margin-bottom: 1rem;
}

.todo.completed span {
    text-decoration: line-through;
}

.todo label {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    padding: 1rem;
    background: #f3f3f3;
}

.todo i {
    z-index: 2;
}